import { makeStyles, Typography } from "@material-ui/core";
import { observer } from "mobx-react";
import React, { useEffect } from "react";
import { useHistory, useRouteMatch } from "react-router";
import { useLendingNetworksStore } from "../../store/storeHooks";
import { NetworksList } from "./NetworksList";

const useStyles = makeStyles(({ spacing }) => ({
  root: {
    paddingTop: spacing(10),
  },
  title: {
    paddingBottom: spacing(2),
  },
}));

export const AllNetworksPage: React.FC = observer(() => {
  const classes = useStyles();
  const history = useHistory();
  const { url } = useRouteMatch();
  const lendingNetworksStore = useLendingNetworksStore();
  // const { isLoading, lendingNetworks } = useLendingNetworksStore();
  const activeLendingNetworkId = lendingNetworksStore.activeLendingNetworkId;
  // Reset the 'active ln' when entering this page
  useEffect(() => {
    console.log(`In effect ${activeLendingNetworkId}`);
    if (activeLendingNetworkId !== "") {
      lendingNetworksStore.setActiveLn("");
    }
  }, [activeLendingNetworkId, lendingNetworksStore]);

  const onViewNetwork = React.useCallback(
    (networkId: string) => {
      const targetUrl = `${url}/${networkId}`;
      history.push(targetUrl);
    },
    [history, url]
  );

  return (
    <div className={classes.root}>
      <Typography variant="h3" align="center" className={classes.title}>
        Ola Lending Networks
      </Typography>
      <Typography variant="h5" align="center">
        A Leap Forward in Decentralized Peer-to-Pool Lending
      </Typography>
      {lendingNetworksStore.isLoading ? (
        <Typography>Loading...</Typography>
      ) : (
        <NetworksList
          lendingNetworksGists={lendingNetworksStore.allLNGists}
          onViewNetwork={onViewNetwork}
        />
      )}
    </div>
  );
});
