import React from "react";
import { makeStyles, Typography } from "@material-ui/core";

export type TModalFooterMessagePayload = {
  title: string;
  tokenAmount: number;
  tokenSymbol?: string;
};

interface IProps extends TModalFooterMessagePayload {}

const useStyles = makeStyles((theme) => ({
  footer: {
    width: "100%",
    display: "flex",
    justifyContent: "space-between",
  },
  title: {
    // fontWeight: "bolder",
  },
}));

export const ModalFooter = React.memo<IProps>((props) => {
  const classes = useStyles();
  const { tokenSymbol, tokenAmount, title } = props;

  return (
    <div className={classes.footer}>
      <Typography className={classes.title}>{title}</Typography>
      <Typography>
        {tokenAmount.toLocaleString()} {tokenSymbol}
      </Typography>
    </div>
  );
});
