export const Header = observer<React.FunctionComponent<IProps>>((props) => {
  const classes = useStyles();

  const cryptoWalletConnectionStore = useCryptoWalletIntegrationStore();
  const modalsStore = useModalsStore();
  const lendingNetworksStore = useLendingNetworksStore();
  const activeLn = lendingNetworksStore.activeLNStore;

  const walletText = cryptoWalletConnectionStore.isConnectedToWallet
    ? summarizeAddress(cryptoWalletConnectionStore.mainAddress)
    : "Connect";

  const hasActiveLn = !!activeLn;
  const activeLnName = activeLn?.name;
  const linkToLnPage = activeLn?.lnHomePage || "/";
  const incentiveTokenBalance = activeLn?.compBalanceUnits || 0;

  return (
    <header className={classes.header}>
      <ContentContainer>
        <Grid
          container
          direction="row"
          alignItems="center"
          style={{
            alignItems: "center",
            alignContent: "center",
          }}
        >
          {/* Logo */}
          <Grid item xs={3} className={classes.alignedRow}>
            {/* TODO : Add Project logo here */}
            {/*<Link className={classes.logoLink} to="/" />*/}
            <Link className={classes.logoLinkText} to="/">
              Ola Finance
            </Link>

            {activeLnName && (
              <a
                className={classes.lnLinkText}
                target="_blank"
                href={linkToLnPage}
              >
                {activeLnName}
              </a>
            )}
          </Grid>
          {/* Empty spaces */}
          {/* DEV_NOTE : Place holder for the navigations */}
          <Hidden xsDown>
            <Grid item xs={6}></Grid>
          </Hidden>

          <Grid
            className={clsx(classes.alignedRow, classes.actions)}
            item
            xs={9}
            sm={3}
          >
            {/* Token balance */}
            {hasActiveLn && (
              <div
                className={clsx([
                  classes.headerButtonLike,
                  classes.tokenBalance,
                ])}
                onClick={() => modalsStore.showIncentiveModal()}
              >
                {incentiveTokenBalance.toFixed(4).toLocaleString()}
                <div
                  className={clsx(
                    classes.tokenIcon,
                    classes.iconComp,
                    classes.icon
                  )}
                />
              </div>
            )}

            {/* Address */}
            {/* DEV_NOTE : Hides on mobile to keep original UX*/}
            <Hidden xsDown>
              <a
                className={clsx(classes.headerButtonLike, classes.account)}
                onClick={() => cryptoWalletConnectionStore.askToConnect()}
              >
                <span />
                {walletText}
              </a>
            </Hidden>
          </Grid>
        </Grid>
      </ContentContainer>
    </header>
  );
});

/**
 * QuicknDirty
 */
function summarizeAddress(address: string): string {
  return `${address.substring(0, 3)}...${address.substring(
    address.length - 3,
    address.length
  )}`;
}
